.tileContainer {
  width: 400px;
  height: 100%;
  padding: 20px;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.5s;
  border-radius: 10px;
}

.backgroundSalmon{
  background-color: #343330;
  color: rgb(198, 197, 197);
  transition: 0.5s;
}

.backgroundSalmon:hover{
  background-color: #886176;
}

.backgroundBlue{
  background-color: #343330;
  color: rgb(198, 197, 197);
  transition: 0.5s;
}

.backgroundBlue:hover{
  background-color: #384865;
}

.backgroundLightBlue{
  background-color: #343330;
  transition: 0.5s;
  color: rgb(198, 197, 197);
}

.backgroundLightBlue:hover{
  background-color: #5780a5;
}

.backgroundYellow{
  background-color: #343330;
  color: rgb(198, 197, 197);
  transition: 0.5s;
}

.backgroundYellow:hover{
  background-color: #a07d37;
}

.backgroundGreen{
  background-color: #343330;
  color: rgb(198, 197, 197);
  transition: 0.5s;
}

.backgroundGreen:hover{
  background-color: #415844;
}

.tileImage {
  width: 350px;
  height: 350px;
}

.tileImageTopAndBottomBorder{
  padding: 8px;
  background-color: black;
  border-radius: 5px;
}

.tileTextContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 1 1 auto;
  margin-top: 10px;
}

.tileHeadingContainer {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Archivo Narrow", sans-serif;
  font-size: 1em;

}

.tileHeading {
  display: inline;
  font-size: 2em;
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease;
}

.tileSubheaderContainer {
  width: 100%;
  height: 150px;
  font-family: "Cormorant", serif;
  font-size: 1.5em;
  -webkit-transition: all .5s ease;
  -moz-transition: all .5s ease;
  transition: all .5s ease;
}

.tileContainer:hover{
  transform: scale(1.02);
}

@media screen and (max-width: 768px) {
  .tileContainer{
    width: 250px;
  }

  .tileImage {
    width: 250px;
    height: 250px;
  }

  .tileHeadingContainer{
    height: 20px;
  }

  .tileHeading{
    font-size: 1.8em;
  }

  .tileSubheaderContainer{
    font-size: 1.2em;
  }

}

